body {
  position: relative; 
}
.crd{
margin-top:100px;
box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
transition: 0.3s;
width:100%;
}
.crd:hover{
box-shadow: 0 16px 32px 0 rgba(46, 0, 255,0.2);
}
.crd2{
margin-top:100px;
box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
transition: 0.3s;
width:100%;
}
.crd2:hover{
box-shadow: 0 16px 32px 0 rgba(46, 0, 255,0.2);
}
.fa {
padding: 20px;
font-size: 30px;
width: 70px;
text-align: center;
text-decoration: none;
margin: 5px 2px;
}

.fa:hover {
opacity: 0.7;
}

.fa-facebook {
background:#2094FF ;
color: white;
border-radius:15px;
}

.fa-twitter {
background: #2094FF ;
color: white;
border-radius:15px;
}

.fa-linkedin {
background:#2094FF ;
color: white;
border-radius:15px;
}

.fa-instagram {
background:#2094FF ;
border-radius:15px;
color: white;
}

.fa-github {
background: #2094FF ;
color: white;
border-radius:10px;
}
.cont {
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
max-width: 300px;
margin: auto;
text-align: center;
font-family: arial;
}

.title {
color: #0062A9;

}

.bx {
border: none;
outline: 0;
display: inline-block;
padding: 8px;
color: white;
background-color: #0062A9;
text-align: center;
cursor: pointer;
width: 100%;
font-size: 18px;
}

.ax {
text-decoration: none;
font-size: 22px;
color: black;
}

.bx:hover, ax:hover {
opacity: 0.7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
